body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container{

    margin:auto;
    display: grid;
    grid-template-areas: "a"
                         "b";
    
}
.imageDiv{
    grid-area: a;
    margin-left: 32vw;
    position: relative;
}
    .image{
        width:300px;
        height:400px;
       object-fit: contain
    }
    .title{
    background-image: -webkit-linear-gradient(bottom,white,rgb(255, 255, 255,0.90));
    background-image: linear-gradient(to top,white,rgb(255, 255, 255,0.90));
    width: 300px;
    position: absolute;
    bottom: -20px;
    height: 35%;
    align-content: flex-end
    }   
.infoContainer{
    grid-area: b;
    display: flex;
}
.descDiv{
    margin-left: 10vw;

    width:40vw;
}
.mapContainer{
    margin-right: 10vw;

    width:40vw;
}

