.container{
    margin:auto;
    display: grid;
    grid-template-areas: "a"
                         "b";
    
}
.topArea{
    grid-area: a;
    margin-left: 32vw;

}

.eventContainer{
    margin-left: 10vw;
    grid-area: b;
    width:60vw;

    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}
.listItem{
    object-fit: contain;

    border:solid;
    margin:10px;
    height: 200px;
    width: 200px;

    background-repeat: no-repeat;
    background-position: center; 
    position: relative;
    

}
.listItemContent{
    background-image: -webkit-linear-gradient(bottom,white,rgb(255, 255, 255,0.75));
    background-image: linear-gradient(to top,white,rgb(255, 255, 255,0.75));
    width: 100%;
    position: absolute;
    bottom: 0;
}
.listItemButtons{
    padding: 3px;
    width: 100%;
    display: flex;
    justify-content: space-around  
}

.innerDiv{
    margin: auto;
    padding-top:5px;
}
.searchDiv{
    display:flex;
    justify-content: space-between
}
.searchButton{
    height: 30px;
}
.location-search-input{
    width:300px;
}
.container{
    width: 95vw;
    display: grid;
    grid-template-columns:  repeat(8,1fr);
    grid-template-rows: auto auto;


}
.navBar{
    grid-column: 1/-1;

    display: flex;
    flex-direction: row;
    justify-content: space-between
}
    .navBarName{
        width: 400px
    }
    .buttonsContainer{
        align-self: flex-end;
        width:200px;
        padding: 15px
    }


.leftContent{
    grid-column: 1/2;
    background:red;
}
.rightContent{
    
    
    grid-column: 2/-1;

    width:100%;
    display: grid;

}
