.container{
    margin:auto;
    display: grid;
    grid-template-areas: "a"
                         "b";
    
}
.topArea{
    grid-area: a;
    margin-left: 32vw;

}

.eventContainer{
    margin-left: 10vw;
    grid-area: b;
    width:60vw;

    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}
.listItem{
    object-fit: contain;

    border:solid;
    margin:10px;
    height: 200px;
    width: 200px;

    background-repeat: no-repeat;
    background-position: center; 
    position: relative;
    

}
.listItemContent{
    background-image: linear-gradient(to top,white,rgb(255, 255, 255,0.75));
    width: 100%;
    position: absolute;
    bottom: 0;
}
.listItemButtons{
    padding: 3px;
    width: 100%;
    display: flex;
    justify-content: space-around  
}